/*
# Main Menu
--------------------------------*/
.site-navigation {
    position: absolute;
    left: 0;
    z-index: 9999;
    width: 100%;
    height: 22px;
    overflow: hidden;
    background: #fff;
    list-style: none;
    transition: all .35s;
}

.site-navigation.show {
    height: auto;
}

.site-navigation ul {
    padding: 0;
    margin: 0;
}

.site-navigation ul li {
    display: block;
    padding: 10px 0;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.08rem;
    text-transform: uppercase;
}

.site-navigation ul li a {
    display: block;
    padding: 4px 0;
    border-bottom: 2px solid transparent;
    color: #2f2f2f;
    transition: all 0.35s;
    text-decoration: none;
}

.site-navigation ul li a:hover,
.site-navigation ul li.current-menu-item a {
    border-color: #2f2f2f;
}

@media screen and (min-width: 768px) {
    .site-header {
        margin-top: 12px;
        margin-bottom: 40px;
    }
}

@media screen and (min-width: 992px) {
    .site-navigation ul li {
        padding: 0 20px;
    }
}

/*--------------------------------------------------------------
# Hero Section
--------------------------------------------------------------*/


.hero-slider img {
    width: 100%;
    max-height: 450px;
}

.swiper-container-horizontal>.swiper-pagination-bullets,
.swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: 30px;
    left: 38px;
    text-align: left;
    
}

.swiper-button-next,
.swiper-button-prev {
    visibility: hidden;
    width: 54px;
    height: 54px;
    margin-top: -27px;
    background: #fff;
    transition: all .35s;
}

.swiper-button-next {
    right: 42px;
}

.swiper-button-prev {
    left: 42px;
}

.swiper-button-next:hover,
.swiper-button-prev:hover {
    opacity: 1;
}

.swiper-button-next span,
.swiper-button-prev span {
    display: block;
    width: 12px;
    padding-top: 6px;
}

.swiper-button-next path,
.swiper-button-prev path {
    fill: #3a3a3a;
}

.swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    display: inline-block;
    border-radius: 0;
    background: transparent;
    border: 2px solid #fff;
    opacity: 1;
}

.swiper-pagination-bullet-active {
    background: #fff;
}

@media screen and (min-width: 768px){
    .swiper-button-next,
    .swiper-button-prev {
        visibility: visible;
    }
}


@media screen and (min-width: 768px){
    .yt-subscribe {
        bottom: -250px;
    }

    .yt-subscribe img {
        width: auto;
    }
}

@media screen and (min-width: 992px){
    .yt-subscribe {
        bottom: -200px;
        text-align: center;
    }
}

