    height: 40px;
    font-size: 11px;
    color: #2f2f2f;
    background: #f0f0f0;
    text-decoration: none;
}

.pagination li a:hover,
.pagination li.active a {
    color: #fff;
    background: #2f2f2f;
}

/*--------------------------------------------------------------
# Footer
--------------------------------------------------------------*/
.sit-footer {
    padding-top: 100px;
    border-top: 1px solid #d8d8d8;
}

.footer-post-wrap {
    margin-bottom: 60px;
}

.footer-post-wrap figure {
    width: 185px;
    margin: 0 30px 0 0;
}

.footer-post-wrap figure img {
    display: block;
    width: 100%;
}

.footer-post-cont {
    width: calc(100% - 205px);
}

.footer-post-cont .posted-date {
    margin-top: 20px;
    font-size: 11px;
    letter-spacing: 0.08rem;
    text-transform: uppercase;
    color: #afafaf;
}

.footer-post-cont h3 {
    margin-top: 10px;
    font-size: 18px;
}

.footer-post-cont h3 a {
    color: #2f2f2f;
}

.footer-post-cont .tags-links {
    margin-top: 12px;
}

.footer-post-cont .tags-links a {
    margin-right: 5px;
    font-size: 12px;
    color: #afafaf;
}

.footer-post-cont .read-more {
    display: inline-block;
    margin-bottom: 15px;
    font-size: 14px;
    color: #2f2f2f;
    text-decoration: underline;
    text-transform: capitalize;
}

@media screen and (min-width: 1200px){
    .footer-post-wrap {
        margin-bottom: 0;
    }
}

/*--------------------------------------------------------------
# Footer Instagram
--------------------------------------------------------------*/
.footer-instagram {
    margin-top: 60px;
}

.footer-instagram figure {
    width: 33.333333%;
    margin: 0;
}

.footer-instagram figure img {
    display: block;
    width: 100%;
}

@media screen and (min-width: 576px){
    .footer-instagram figure {
        width: 25%;
        margin: 0;
    }
}

@media screen and (min-width: 768px){
    .footer-instagram figure {
        width: 20%;
        margin: 0;
    }
}

@media screen and (min-width: 992px){
    .footer-instagram figure {
        width: auto;
        margin: 0;
    }
}

@media screen and (min-width: 1200px){
    .footer-instagram {
        margin-top: 100px;
    }

    .footer-instagram figure img {
        display: block;
        width: 100%;
        min-width: 100%;
    }
}

/*--------------------------------------------------------------
# Footer Bar
--------------------------------------------------------------*/
.footer-bar {
    padding: 22px 0;
    background: #2f2f2f;
}

.footer-copyright p {
    margin: 0;
    font-size: 13px;
    color: #b8b8b8;
    text-align: center;
}

.footer-social {
    margin-top: 30px;
}

.footer-social ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.footer-social li {
    margin: 0 15px;
}

.footer-social li a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 14px;
    height: 14px;
    font-size: 14px;
    color: #74797e;
}

.footer-social li a:hover {
    color: #fff;
}

@media screen and (min-width: 768px) {
    .footer-copyright p {
        text-align: left;
    }

    .footer-social {
        margin-top: 0;
    }

    .footer-social li {
        margin: 0 0 0 30px;
    }






}
